function LazyYouTube({ thumbnail,isPersian, disabled, videoId }) {
    const [isLoaded, setLoaded] = useState(false);
    const [isIFrameLoaded, setIFrameLoaded] = useState(false);
    const { ref, inView } = useInView();

    useEffect(() => {
        const timer = setTimeout(() => {
            setLoaded(true);
        }, 700);

        return () => clearTimeout(timer);
    }, []);

    const showEmbed = !disabled && isLoaded;
    const src = showEmbed ? `https://www.youtube.com/embed/${videoId}` : `https://www.youtube.com/watch?v=${videoId}`;

    return (
        <div ref={ref}>
            {!isIFrameLoaded && (
                <a href={src} target="_blank">
                    <div
                        style={{
                            width: '100%',
                            height: 0,
                            paddingBottom: '56.25%',
                            position: 'relative',
                            background: `url(${thumbnail}) no-repeat center center`,
                            backgroundSize: 'cover'
                        }}
                    />
                </a>
            )}
            {showEmbed &&
                <iframe
                    style={!isIFrameLoaded ? {opacity: 0, position: "absolute"} : {}}
                    src={src}
                    title="Steve Moretz Resume Portfolio video"
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    allowFullScreen
                     onLoad={()=>{
                        setTimeout(()=>{
                            setIFrameLoaded(true);
                        },300)
                    }}
                ></iframe>
            }
        </div>
    );
}

function LazyImg(props) {
    // return (
    //     <img {...props}/>
    // )
    return (
        <LazyLoad rootMargin={"5500px"} style={{marginTop: 4}} freezeOnceVisible={true}>
            <img {...props}/>
        </LazyLoad>
    );
}
