const CarouselFit = ({images, horizontal, altImages}) => {
    const [imageWidth, setImageWidth] = useState();
    const [imageHeight, setImageHeight] = useState();

    if (!images) return;

    return (
        <LazyLoad rootMargin={"5500px"} freezeOnceVisible={true}>
            <div style={{width: '100%', display: 'flex', alignItems: 'center', flexDirection: 'column'}}>
                <div style={{display: 'inline-block', position: 'absolute', top: 0, left: 0, opacity: 0}}>
                    <ReactResizeDetector handleWidth handleHeight>
                        {({width, height}) => {
                            if (width && height) {
                                setImageWidth(width);
                                setImageHeight(height);
                            }
                            return (
                                <img src={images[0]}
                                     alt={altImages[0]}
                                     title={altImages[0]}
                                     style={{maxWidth: !horizontal ? '60%' : '100%', objectFit: 'scale-down'}}/>
                            )
                        }}
                    </ReactResizeDetector>;
                </div>
                <div style={{display: 'inline-block', position: 'relative', width: imageWidth, height: imageHeight}}>
                    <Carousel indicators={false} animation='slide' autoPlay={true} style={{marginTop: 100}}>
                        {
                            images.map((item, index) => <img title={altImages[index]} alt={altImages[index]} src={item} key={`p-${index}`} style={{
                                maxWidth: '100%',
                                objectFit: 'scale-down',
                                pointerEvents: 'none'
                            }}/>)
                        }
                    </Carousel>
                </div>
            </div>
        </LazyLoad>
    )
};


const StickyNote = ({children, width, height, top, left, right, color}) => {
    const [propsAnimateSticky, setAnimateSticky, stopAnimateSticky] = useSpring(() => ({
        to: async (next, cancel) => {
            while (1) {
                // next({left: 130});
                // await sleep(8000);
                await next({value: 0.9, config: {mass: 0.1, tension: 5, friction: 0.7}});
                await next({value: 0.92, config: {mass: 0.1, tension: 5, friction: 0.7}});
            }
        }, from: {value: 0.96}, config: {mass: 1, tension: 50, friction: 0.7}
    }));
    return (
        <div className="container" style={{position: 'absolute', left: left, right: right}}>
            <div className="sticky-container">
                <div className="sticky-outer" style={{height: height, width: width, top: top}}>
                    <div className="sticky">
                        <svg width="0" height="0">
                            <defs>
                                <clipPath id="stickyClip" clipPathUnits="objectBoundingBox">
                                    <animated.path
                                        d={propsAnimateSticky.value.interpolate(o => `M 0 0 Q 0 0.69, 0.03
                                            ${o}
                                             0.03 ${0.96 + o * 0.001}, 1
                                            ${o}
                                             Q 0.96 0.69, ${0.96 + o * 0.005} 0 ${0.96 + o * 0.003} 0, 0 0`)}
                                        strokeLinejoin="round"
                                        strokeLinecap="square"
                                    />
                                </clipPath>
                            </defs>
                        </svg>
                        <div className={`sticky-content ${color && `sticky-color-${color}`}`}>
                            {children && children}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

